* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  margin-top: 10px;
  display: flex;
}

.field {
  flex-direction: column;
  margin-right: 15px;
  display: flex;
}

.value {
  text-align: center;
  letter-spacing: 4px;
  color: #0f0f0f;
  text-shadow: 0 6px 6px #c4b59d, 0 -3px 1px #fff;
  font-size: 32px;
  font-weight: 500;
}

[data-seconds] {
  color: #c43131;
}

.label {
  text-align: center;
  text-transform: uppercase;
  color: #0f0f0f;
  text-shadow: 0 6px 6px #c4b59d, 0 -3px 1px #fff;
  font-size: 14px;
  font-weight: 400;
}

input#datetime-picker {
  cursor: pointer;
  text-align: center;
  background: #fafafa;
  border: 1px solid #acaaaa;
  width: 200px;
  height: 26px;
  font-weight: 600;
  box-shadow: 0 6px 8px #c4b59d, 0 -2px 1px #fff;
}

input#datetime-picker:hover, input#datetime-picker :focus {
  color: #0f0f0f;
  border: 1px solid #212121;
}

button {
  cursor: pointer;
  color: #acaaaa;
  border: 1px solid #acaaaa;
  display: inline-block;
  box-shadow: 0 6px 8px #c4b59d, 0 -2px 1px #fff;
}

button:hover, button:focus {
  color: #0f0f0f;
  border: 1px solid #212121;
}

[data-start] {
  width: 48px;
  height: 26px;
}

button[data-start] {
  width: 60px;
  height: 48px;
  position: absolute;
  transform: translate(400px, 250px);
}

[data-stop] {
  width: 60px;
  height: 48px;
  position: absolute;
  transform: translate(500px, 250px);
}

.form {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin: 50px;
  display: flex;
}

input[type="number"] {
  cursor: pointer;
  -webkit-text-fill-color: #c43131;
  text-align: center;
  background: #fafafa;
  border: 1px solid #acaaaa;
  width: 200px;
  height: 26px;
  font-weight: 600;
  box-shadow: 0 6px 8px #c4b59d, 0 -2px 1px #fff;
}

label {
  text-align: center;
  text-transform: uppercase;
  color: #0f0f0f;
  text-shadow: 0 6px 8px #c4b59d, 0 -3px 1px #fff;
  font-size: 14px;
  font-weight: 600;
}

button[type="submit"] {
  width: 150px;
  height: 26px;
  margin-top: 18px;
}

